// ==== FONTS ====================================================================
@font-face {
  font-family: themeFont;
  src: url('Legacy.otf');
}

@font-face {
  font-family: headerFont;
  src: url('Grostino.ttf');
}

@font-face {
  font-family: postTitleFont;
  src: url('BaskervilleLight.ttf');
}

@font-face {
  font-family: postContentFont;
  src: url('BaskervilleLight.ttf');
}

@font-face {
  font-family: navLinkFont;
  src: url('didot/DidotBold.otf');
}

// === SUPER ====================================================================

* {
  box-sizing: border-box;
}

html {
    margin: 0px;
    padding: 0px;
}

body {
 	margin: 0px;
  padding: 0px;
	font-family: postTitleFont;
	background-color: #eeeeee;
	background-repeat: no-repeat;
	color: #313233
}


// ==== NAVBAR================================================================

.navbar {
	height: 100px;
	float: right;
	display: inline-block;
}

.current {
 	color: #ffffff;
}

.uncurrent {
	color: #a18497;
}

#navlink {
	margin-top: 13px;
	margin-right: 24px;
	font-size: 18px;
	font-family: navLinkFont;
  float: right;
  top: 85%;
  -ms-transform: translateY(85%);
  transform: translateY(85%);
}

.separator-basic {
	height: 2px;
	width: 100%;
	margin: 0px;
	padding: 0px;
	background-color: #777777;
}

a {
	color: #a05b3c;
	text-decoration: none;
}

// ==== Header================================================================

.headerbar {
	height: 100px;
	overflow: hidden;
	color: #000000;
	//color: #a18497;
	//background-color: #40384a;
	background-color: #eeeeee;
	font-family: headerFont;
	position: relative;
	font-size: 46px;
	letter-spacing: 5px;
	width: 49%;
	border: 0px solid #999999;
	display: inline-block;
}

.headerverticalcenter {
  margin: 0;
  position: absolute;
  top: 60%;
  -ms-transform: translateY(-45%);
  transform: translateY(-45%);
  float: left;
}

.leftspacedtitle {
display: inline;
	margin-top: 0px;
	margin-bottom: 10px;
   	padding: 10px;
   	font-family: headerFont;
}


// ==== POST ===================================================================

.postTitle {
		font-family: postTitleFont;
		color: #a03a2b
}

.postContent {
		font-family: postContentFont;
}

.fullheader {
background-color: #eeeeee;
	//background-color: #40384a;
}

p {
	line-height: 1.6;
}

// ==== RESPONSIVE ====================================================================

.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}

[class*="col-"] {
  float: left;
  padding: 15px;
  margin: 5px;
  overflow: hidden;
  border: 1px solid #999999;
}

.unborderCol {
	border: 0px solid #999999;
}

.savedextra {
	  background-color: #63a583;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

// ==== SIDEBAR =====================================================================

.sidebarcat {
	background-color: #d4d6d9;
	padding: 15px;
	font-family: navLinkFont;
}

// ==== CENTRE CONTENT================================================================

.maincontent {
	box-shadow: 3px 6px #999999;
}

// ===== FOOTER ===================================================================

footer {
  text-align: center;
  margin-top: 50px;
  padding: 3px;
  background-color: #777777;
  color: white;
}

// ==== RESPONSIVE ================================================================

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
    width: 98%;
  };

  .headerbar {
  	font-size: 15px;
  }
}